
.resultStr::-webkit-scrollbar {
    width: 8px;
}
.resultStr::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.3);
    border-radius: 10px;
}
.resultStr::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.3);
}

.wrapper {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;


}
:root{
    --mainWidth:55vh;
}
.table {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    border: 2px silver solid;
    border-radius: 5px;
    width: var(--mainWidth);
    height: 80vh;
    margin: 30px auto;
    flex-shrink: 2;
    box-shadow: gray inset 0 0 2px 2px;

}

.tbody{
    height:70vh ;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.view {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    font-weight: bold;
    border-bottom: #cccccc solid 0.5vh;
    flex-wrap: wrap;
    width: 99%;
    height: 20vh;

}
.resultStr{
    width: calc(var(--mainWidth) - 1%);
    display: block;
    height: 7vh;
    font-size: 2vh;
    border-bottom: #cccccc 2px solid;
    overflow-y: scroll;
    overflow-x: hidden;
}
.currNum{ border-bottom: #cccccc 2px solid;
    height: 5vh;
    display: block;
    font-size: 2vh;
}
.result{
    height: 5vh;
    display: block;
    font-weight: bold;
    font-size: 2vh;

}

.btn:hover {
    box-shadow: inset 0 0 2px 2px #cccccc;
}
.row {

    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    width: 60vh;

}

.btn {
    font-size: 5vh;
    font-weight: bold;
    border: #cccccc solid 0.2vh;
    border-radius: 1vh;
    width: 10vh;
    height: 10vh;
    display: flex;
    align-items: center;
    justify-content: center



}